@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

* {
  font-family: "Raleway", sans-serif;

}

body, html {
  margin: 0;
}

.App {
  background-color: bisque;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.todo {
  width: 450px;
  background-color: #F2FDFF;
  border-radius: 5%;
  height: 60%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
}

.title {
  gap: 1rem;
  display: flex;
  justify-content: center;
}

.title img {
  margin-top: 20px;
  width: 50px;
  height: 50px;
}

.title h1 {
  font-size: 35px;
}

.input-box {
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 2%;
  display: flex;
  justify-content: center;
  width: 350px;
  height: 45px;
  background-color: rgb(220, 228, 228);
  border-radius: 40px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
}

.input-box input {
  background-color: transparent;
  border: none;
  outline: none;
  flex: 1;
  padding-left: 20px;
}

.input-box input::placeholder {
  opacity: 0.9;
}

.input-box button {
  font-size: 15px;
  font-weight: 800;
  width: 100px;
  padding-left: 20px;
  padding-right: 20px;
  border: 0;
  border-radius: 30px; 
  background-color: #0B2545;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.input-box button:hover {
  background-color: #1A4E7A; /* Change color on hover */
  transform: scale(1.05); /* Slightly scale up the button */
}

.items {
  width: 350px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  gap: 2rem;
}

.listing {
  display: flex;
  align-items: center;
  flex: 1;
  cursor: pointer;
  gap: 1rem;
}

.listing img {
  width: 30px;
  height: 30px;
}

.delete {
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.completed{
  text-decoration: line-through;
  color: gray;
}